$twitterBorder: #e6ecf0;
$twitterMainColor: #ff6600;
$twitterComment: #6c757d;
$popularsBackground: #fafafb;

.profile-wrapper {
    width: 50%;
    height: 100vh;
    position: fixed;
    margin-left: 25%;
    border-right: 1px solid $twitterBorder;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 8px;
      }
      &::-webkit-scrollbar-button {
        background: transparent;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #c1c1c1;
        border-radius: 4px;
        }
      &::-webkit-scrollbar-track {
        box-shadow: transparent;
      }
  
    .profileHeader {
      width: 50%;
      height: 74px;
      display: flex;
      align-items: center;
      position: fixed;
      top: 0;
      padding: 15px;
      background-color: #fff;
      font-weight: 700;
      font-size: 18px;
      border-right: 1px solid $twitterBorder;

      .arrow {
        margin: 0 10px;
        cursor: pointer;
      }

      .backUser {
        margin-left: 10px;

        .topUsername {
            margin: 0;
        }
        .userTweetsNum {
            margin: 0;
            font-size: 13px;
            font-weight: 500;
            color: $twitterComment;
        }
      }
    }
  }